@import './_variables';
@import './fonts.scss';
@import './reset.scss';

html {
    scroll-behavior: smooth;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Lato', sans-serif;
    background: linear-gradient(190.34deg, $dark-blue-gradient 0.28%, $light-blue-gradient 100%);
    width: 100%;
}

#home::before {
    content: '';
    display: block;
    height: 80px;
    margin-top: -80px;
}

.card {

    &--discover__style,
    &--offers__style,
    &--space__style,
    &--dream__style,
    &--fullfill__style {
        background-size: cover;
        background-position: center;
        padding: 80px 90px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 490px;
        color: $primary-white;
        border-radius: 10px;

        &--offers__style,
        &--fullfill__style {
            background-position: center;
            padding: 35px;
            border-radius: 10px;
            justify-content: start;
            height: 350px;
        }

        &--space__style,
        &--dream__style {
            padding: 20px 35px;
            justify-content: center;
            align-items: flex-start;
            height: 60vh;
            color: $primary-white;
            flex-direction: column;
            border-radius: 10px;
            gap: 20px;
        }
    }

    &--discover__style {
        background-image:
            linear-gradient($dark-overlay-gradient, $dark-overlay-gradient),
            url('./assets/DiscoverImage.svg');
    }

    &--offers__style {
        background-image:
            linear-gradient($dark-overlay-gradient, $dark-overlay-gradient),
            url('./assets/MoveBordersImage.svg');
        height: 350px;
        padding: 35px;
        grid-area: first;
    }

    &--space__style {
        background-image:
            linear-gradient($dark-overlay-gradient, $dark-overlay-gradient),
            url('./assets/SpaceImage.svg');
        padding: 50px 35px;
        height: 350px;
        grid-area: second;
    }

    &--dream__style {
        background-image:
            linear-gradient($dark-overlay-gradient, $dark-overlay-gradient),
            url('./assets/DreamsImage.svg');
        padding: 50px 35px;
        height: 350px;
        grid-area: third;
    }

    &--fullfill__style {
        background-image:
            linear-gradient($dark-overlay-gradient, $dark-overlay-gradient),
            url('./assets/FullFillImg.svg');
        height: 350px;
        padding: 35px;
        grid-area: fourth;
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        gap: 22px;

        &[id='#borders-card'] {
            h2 {
                &::after {
                    content: '- it\'s possible with us!';
                }
            }
        }
    }

    &--button {
        font-family: Lato;
        font-size: 22px;
        font-weight: 700;
        line-height: 26.4px;
        width: 191px;
        height: 54px;
        border-radius: 100px;
        cursor: pointer;
        background-color: $primary-yellow;
        border: none;

        &__filled {
            @extend .card--button;

            &:hover {
                background-color: $primary-black;
                color: $primary-yellow
            }
        }

        &__outlined {
            @extend .card--button;
            background-color: transparent;
            color: $primary-yellow;
            outline: 1px solid $primary-yellow;

            &:hover {
                background-color: $primary-yellow;
                color: $primary-black
            }
        }
    }

    &--title__style {
        color: $primary-pink;
    }

    &--subtitle__style {
        color: $primary-yellow;
    }
}

h1 {
    font-family: Lato;
    font-size: 5.77vw;
    font-weight: 800;
    line-height: 5.77vw;
}

h2 {
    font-family: Lato;
    font-size: 2.6vw;
    font-weight: 800;
    line-height: 38.4px;
}

.header {
    &__style {
        top: 0;
        display: flex;
        padding: 24px 81px;
        justify-content: space-between;
        background-color: $primary-black;
        position: fixed;
        width: 100%;
        height: 80px;
        z-index: 9;
    }

    &__container {
        display: flex;
        justify-content: space-between;
        max-width: 300px;
        width: 100%;
        align-items: center;
    }

    &--link__style {
        text-decoration: none;
        color: $primary-white;
        margin-right: 20px;
    }

    &__logo {
        width: 120px;
        cursor: pointer;
    }

    &__cart {
        cursor: pointer;
    }

    .burger__menu {
        display: none;
        cursor: pointer;
    }

    .burger-icon,
    .close-icon {
        display: none;
        width: 30px;
        height: 30px;
    }

    .menu-toggle {
        display: none;
    }
}

.footer {
    &__style {
        position: relative;
        text-align: center;
        height: 127px;
        align-items: center;
        background-color: $primary-black;
        justify-content: center;
        bottom: 0px;
        flex-direction: column;
    }

    &--text {
        font-family: Lato;
        font-size: 18px;
        font-weight: 800;
        line-height: 28px;
        text-align: center;
        color: $primary-yellow;
    }
}

.cosmo__wrapper {
    margin-top: 80px;
}

.offers-and-annotation {
    display: flex;

    &__wrapper {
        padding: 60px 80px;
    }

    &__style {
        font-family: Lato;
        font-size: 32px;
        font-weight: 800;
        line-height: 38.4px;
        color: $primary-white;

        &::before {
            content: '';
            display: block;
            height: 80px;
            margin-top: -80px;
        }
    }

    &__container {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-template-rows: auto;
        margin-top: 30px;
        gap: 40px;
        grid-template-areas:
            "first first first first"
            "second second third third"
            "fourth fourth fourth fourth";
    }
}

.two-cards {
    display: flex;
    font-family: Lato;

    &__container {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &--title__style {
        font-size: 3.7vw;
        font-weight: 800;
        line-height: 4vw;
    }

    &--subtitle__style {
        font-size: 1.95vw;
        font-weight: 800;
        line-height: 2.5vw;
    }

    h1 {
        font-family: Lato;
        font-size: 40px;
        font-weight: 800;
        line-height: 40px;
    }

    h2 {
        font-family: Lato;
        font-size: 24px;
        font-weight: 800;
        line-height: 28.8px;
    }
}

.annotation {
    font-family: Lato;

    &--text {
        display: flex;
        flex-direction: column;
        margin-top: 62px;
        gap: 17px;
    }

    &--title {
        font-size: 32px;
        font-weight: 800;
        line-height: 38.4px;
        color: $primary-white;
    }

    &--subtitle {
        font-size: 20px;
        font-weight: 700;
        line-height: 24px;
        text-align: left;
        color: $primary-ligth-gray;
    }

    &--readmore {
        font-size: 20px;
        font-weight: 800;
        line-height: 24px;
        text-decoration: none;
        color: $primary-yellow;
    }
}

.image__wrapper {
    width: 820px;

    img {
        position: absolute;
        top: 112px;
        right: 1px;
    }
}

.menu-toggle {
    display: none;
}

.burger-icon {
    display: none;
}

@media (max-width: 1248px) {
    .card {
        &__wrapper {
            display: flex;
            flex-direction: column;
            gap: 22px;
            max-width: 630px;
        }

        &--discover__style {
            background-size: cover;
            background-position: center;
            padding: 80px 90px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: inherit;
            color: $primary-white;
            border-radius: 10px;
        }

        &--space__style, &--dream__style{
            padding: 0px;
        }
    }

    .two-cards {
        &__container {
            display: flex;
            flex-direction: column;
            padding: 35px;
            justify-content: space-between;
            height: 100%;
        }

        &--title__style {
            max-width: 280px;
        }
    }
}

@media (max-width: 768px) {

    html,
    body {
        overflow-x: hidden;
    }

    .offers-and-annotation {

        &__container {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            grid-template-rows: auto;
            gap: 30px;
            grid-template-areas:
                "first first second second"
                "third third fourth fourth"
        }

        &__wrapper {
            padding: 55px 30px;
        }

        &__style {
            display: flex;
            font-size: 24px;
            line-height: 28.8px;
        }
    }

    .card {
        &--discover__style {
            position: relative;
            padding: 80px 0px 80px 30px;
            overflow-x: hidden;

            h1 {
                font-family: Lato;
                font-size: 48px;
                font-weight: 800;
                line-height: 48px;
                max-width: 390px;
            }

            h2 {
                font-size: 22px;
                font-weight: 800;
                line-height: 26.4px;
            }

            img {
                position: absolute;
                right: -110px;
                top: 17px;
                width: 355px;
                height: 355px;
            }
        }

        &__wrapper {
            justify-content: space-between;

            &[id='#borders-card'] {
                h2 {
                    &::after {
                        content: '';
                        display: none;
                    }
                }
            }
        }

        &--offers__style,
        &--space__style,
        &--dream__style,
        &--fullfill__style {

            padding: 87px 15px 30px 26px;
            height: auto;
            margin: 0px;

            h1 {
                font-size: 40px;
                font-weight: 800;
                line-height: 40px;
                max-width: 245px;
                height: 150px;
            }

            h2 {
                font-size: 16px;
                font-weight: 800;
                line-height: 19.2px;
            }
        }

        &--button__outlined {
            bottom: 35px;
            max-width: 161px;
            min-width: 110px;
            height: 33px;
            gap: 10px;
            border-radius: 100px;
            font-size: 16px;
            font-weight: 700;
            line-height: 19.2px;
            text-align: center;
        }
    }

    .two-cards__container{
        padding: 0%;
    }

    .header {
        &__style {
            padding: 28px 0px;
        }

        &__container {
            justify-content: space-around;
        }
    }

    .image__wrapper {
        width: 350px;
    }

    .annotation {
        &--title {
            font-size: 24px;
            font-weight: 800;
            line-height: 28.8px;
        }

        &--subtitle {
            font-size: 16px;
            font-weight: 700;
            line-height: 19.2px;
        }

        &--readmore {
            font-size: 16px;
            font-weight: 800;
            line-height: 19.2px;
        }
    }
}

@media (max-width: 525px) {
    .offers-and-annotation__container {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-template-rows: auto;
        gap: 30px;
        grid-template-areas:
            "first first first first"
            "second second second second"
            "third third third third"
            "fourth fourth fourth fourth"
    }

    .card {
        &--discover__style {
            padding: 80px 0px;
            flex-direction: column-reverse;
            overflow: hidden;

            figure {
                position: relative;
                top: -200px;
                left: -115px;
                height: auto;
            }
        }

        &--button__filled {
            align-self: center;
            height: 33px;
            width: 161px;
            font-family: Lato;
            font-size: 16px;
            font-weight: 700;
            line-height: 19.2px;
        }

        &__wrapper {


            &[id='earth_card'] {
                margin-top: 170px;

                h1 {
                    font-size: 36px;
                    font-weight: 800;
                    line-height: 36px;
                    text-align: center;
                }

                h2 {
                    font-size: 16px;
                    font-weight: 800;
                    line-height: 19.2px;
                    text-align: center;
                }
            }
        }

        &--discover__style,
        &--offers__style,
        &--space__style,
        &--dream__style,
        &--fullfill__style {
            h1 {
                height: auto;
            }
        }
    }

    .header__container {
        display: none;
        flex-direction: column;
        align-items: flex-start;
        background-color: $primary-black;
        top: 80px;
        right: 0;
        width: 100%;
        max-width: 100%;
        position: absolute;
    }

    .header {
        &--link__style {
            border-bottom: 1px solid $primary-white;
            padding-bottom: 15px;
            padding: 15px 0;
            display: block;
            width: 100%;
            text-align: center;
        }

        &__cart {
            align-self: center;
        }
    }

    .burger__menu {
        display: block;
        position: absolute;
        right: 20px;
        top: 25px;
    }

    .burger__menu .burger-icon {
        display: flex;
    }

    .menu-toggle:checked+.burger__menu .burger-icon {
        display: flex;
    }

    .menu-toggle:checked+.burger__menu+.header__container {
        display: flex;
    }

    .menu-toggle {
        opacity: 0;
    }

    .annotation {
        &--title {
            font-size: 24px;
            font-weight: 800;
            line-height: 28.8px;
        }

        &--subtitle {
            font-size: 16px;
            font-weight: 700;
            line-height: 19.2px;
        }

        &--readmore {
            font-size: 16px;
            font-weight: 800;
            line-height: 19.2px;
        }
    }

    .offers-and-annotation__wrapper {
        padding-left: 20px;
        padding-right: 20px;
    }

    div .two-cards--subtitle__style {
        height: auto;
    }

    .card {

        &--offers__style,
        &--fullfill__style {
            h2 {
                height: auto;
            }
        }
    }
}