@font-face {
    font-family: 'Lato';
    src: url('./fonts/Lato.ttf') format('woff2'),
        url('./fonts/Lato.ttf') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Lato Extra Bold';
    src: url('./fonts/LatoExtraBold.ttf') format('woff2'),
        url('./fonts/LatoExtraBold.ttf') format('woff');
    font-weight: bold;
    font-style: normal;
}